'use client'

import { useState, useRef, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { useAuth } from '@/contexts/AuthContext'
import { uploadViatico, getCurrentUser, closeDay } from '@/services/api'
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert'
import { Badge } from '@/components/ui/badge'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle } from '@/components/ui/dialog'
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from '@/components/ui/tooltip'
import { FileCheck, Upload, Camera, X, Loader2, AlertCircle, CheckCircle2, Info, FileText, RefreshCcw, Clock, CalendarDays, Lock } from 'lucide-react'
import Layout from '@/components/Layout'
import AuthGuard from '@/components/AuthGuard'

export default function NuevoViaticoPage() {
  const { appUser, loading: authLoading } = useAuth()
  const router = useRouter()
  const [files, setFiles] = useState<File[]>([])
  const [previews, setPreviews] = useState<{ url: string; isPdf: boolean; name: string }[]>([])
  const [monto, setMonto] = useState('')
  const [descripcion, setDescripcion] = useState('')

  // Nuevos campos del formulario
  const [para, setPara] = useState('')
  const [tipoComprobante, setTipoComprobante] = useState('')
  const [numeroDocumento, setNumeroDocumento] = useState('')
  const [numeroComprobante, setNumeroComprobante] = useState('')
  const queSustenta = 'VIATICO' // Siempre VIATICO (constante)

  // Mantener tipo por compatibilidad con backend (puede ser removido despu칠s)
  const [tipo, setTipo] = useState('otro')

  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState(false)
  const [showCamera, setShowCamera] = useState(false)
  const [facingMode, setFacingMode] = useState<'user' | 'environment'>('environment')
  const fileInputRef = useRef<HTMLInputElement>(null)
  const videoRef = useRef<HTMLVideoElement>(null)
  const streamRef = useRef<MediaStream | null>(null)

  // Estado para l칩gica de fechas
  const [activeDate, setActiveDate] = useState<string>('')
  const [activeDateDisplay, setActiveDateDisplay] = useState<string>('')
  const [timeLeft, setTimeLeft] = useState<string>('')
  const [isGracePeriod, setIsGracePeriod] = useState(false)
  const [lastClosedDate, setLastClosedDate] = useState<string | null>(null)
  const [closingDay, setClosingDay] = useState(false)

  useEffect(() => {
    // Cargar datos del usuario para obtener last_closed_date
    getCurrentUser().then(data => {
      if (data?.user?.last_closed_date) {
        setLastClosedDate(data.user.last_closed_date)
      }
    }).catch(console.error)
  }, [])

  useEffect(() => {
    const timer = setInterval(() => {
      calculateActiveDate()
    }, 1000)

    calculateActiveDate() // Ejecutar inmediatamente

    return () => clearInterval(timer)
  }, [lastClosedDate])

  const calculateActiveDate = () => {
    const now = new Date()
    const dateOptions = { timeZone: 'America/Lima', year: 'numeric', month: '2-digit', day: '2-digit' } as const
    const timeOptions = { timeZone: 'America/Lima', hour: '2-digit', minute: '2-digit', second: '2-digit', hour12: false } as const

    const peruDateParts = new Intl.DateTimeFormat('es-PE', dateOptions).formatToParts(now)
    const peruTimeParts = new Intl.DateTimeFormat('es-PE', timeOptions).formatToParts(now)

    const getPart = (parts: Intl.DateTimeFormatPart[], type: string) => parts.find(p => p.type === type)?.value || ''

    const year = parseInt(getPart(peruDateParts, 'year'))
    const month = parseInt(getPart(peruDateParts, 'month'))
    const day = parseInt(getPart(peruDateParts, 'day'))
    const hour = parseInt(getPart(peruTimeParts, 'hour'))

    const todayString = `${year}-${month.toString().padStart(2, '0')}-${day.toString().padStart(2, '0')}`

    const yesterdayDate = new Date(year, month - 1, day - 1)
    const yesterdayString = `${yesterdayDate.getFullYear()}-${(yesterdayDate.getMonth() + 1).toString().padStart(2, '0')}-${yesterdayDate.getDate().toString().padStart(2, '0')}`

    // L칩gica id칠ntica al backend
    let currentActiveDate = todayString
    let inGracePeriod = false

    if (hour < 10) {
      if (lastClosedDate !== yesterdayString) {
        currentActiveDate = yesterdayString
        inGracePeriod = true

        // Calcular tiempo restante para las 10 AM
        const cutoffTime = new Date(now)
        cutoffTime.setHours(10, 0, 0, 0)
        // Ajustar si cutoffTime ya pas칩 (no deber칤a si hour < 10, pero por seguridad)

        const diff = cutoffTime.getTime() - now.getTime()
        if (diff > 0) {
          const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
          const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60))
          const seconds = Math.floor((diff % (1000 * 60)) / 1000)
          setTimeLeft(`${hours}h ${minutes}m ${seconds}s`)
        } else {
          setTimeLeft('00:00:00')
        }
      } else {
        currentActiveDate = todayString
      }
    } else {
      currentActiveDate = todayString
    }

    setActiveDate(currentActiveDate)
    setIsGracePeriod(inGracePeriod)

    // Formato para mostrar: DD/MM/YYYY
    const [y, m, d] = currentActiveDate.split('-')
    setActiveDateDisplay(`${d}/${m}/${y}`)
  }

  const handleCloseDay = async () => {
    if (!confirm(`쮼st치s seguro de cerrar el d칤a ${activeDateDisplay}? Esta acci칩n no se puede deshacer y los siguientes vi치ticos se registrar치n con fecha de hoy.`)) {
      return
    }

    setClosingDay(true)
    try {
      await closeDay(activeDate)
      setLastClosedDate(activeDate) // Actualizar estado local para reflejar cambio inmediato
      // El useEffect recalcular치 y cambiar치 la fecha activa a hoy
    } catch (e) {
      setError('Error al cerrar el d칤a: ' + (e as Error).message)
    } finally {
      setClosingDay(false)
    }
  }

  useEffect(() => {
    if (!authLoading && appUser && !appUser.crear_carpeta) {
      router.push('/dashboard')
    }
  }, [appUser, authLoading, router])

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFiles = e.target.files
    if (!selectedFiles) return

    const newFiles = Array.from(selectedFiles)

    let currentFiles = [...files];
    let currentPreviews = [...previews];

    for (const file of newFiles) {
      if (file.size > 10 * 1024 * 1024) {
        setError(`El archivo ${file.name} es demasiado grande. M치ximo 10MB.`)
        continue;
      }
      currentFiles.push(file);
      const isPdf = file.type === 'application/pdf'
      const reader = new FileReader()
      reader.onload = (event) => {
        currentPreviews.push({
          url: event.target?.result as string,
          isPdf: isPdf,
          name: file.name,
        })
        setPreviews([...currentPreviews]);
      }
      reader.readAsDataURL(file)
    }
    setFiles(currentFiles);
    setError('')
    if (fileInputRef.current) {
      fileInputRef.current.value = ''
    }
  }

  const removeFile = (index: number) => {
    setFiles(files.filter((_, i) => i !== index))
    setPreviews(previews.filter((_, i) => i !== index))
  }

  const startCamera = () => {
    setShowCamera(true)
    setFacingMode('environment')
  }

  const stopCamera = () => {
    if (streamRef.current) {
      streamRef.current.getTracks().forEach(track => track.stop())
      streamRef.current = null
    }
    if (videoRef.current) {
      videoRef.current.srcObject = null
    }
  }

  const switchCamera = () => {
    setFacingMode(prev => prev === 'user' ? 'environment' : 'user')
  }

  useEffect(() => {
    if (showCamera) {
      stopCamera()

      const constraints = {
        video: {
          facingMode: facingMode
        }
      }

      navigator.mediaDevices.getUserMedia(constraints)
        .then((stream) => {
          streamRef.current = stream
          if (videoRef.current) {
            videoRef.current.srcObject = stream
          }
        })
        .catch((err) => {
          console.error('Error accediendo a la c치mara:', err)
          setError('No se pudo acceder a la c치mara. Aseg칰rate de dar permisos.')

          // Fallback if specific facingMode fails
          if (err.name === 'OverconstrainedError') {
            navigator.mediaDevices.getUserMedia({ video: true })
              .then((stream) => {
                streamRef.current = stream
                if (videoRef.current) {
                  videoRef.current.srcObject = stream
                }
              })
              .catch(e => {
                console.error('Fallback camera error:', e)
                setShowCamera(false)
              })
          } else {
            setShowCamera(false)
          }
        })
    } else {
      stopCamera()
    }

    return () => {
      stopCamera()
    }
  }, [showCamera, facingMode])

  const capturePhoto = () => {
    const video = videoRef.current
    const canvas = document.getElementById('canvas') as HTMLCanvasElement
    if (!video || !canvas) return

    const context = canvas.getContext('2d')
    if (!context) return

    canvas.width = video.videoWidth
    canvas.height = video.videoHeight
    context.drawImage(video, 0, 0)

    canvas.toBlob((blob) => {
      if (blob) {
        const newFile = new File([blob], `foto-${Date.now()}.jpg`, { type: 'image/jpeg' })
        setFiles([...files, newFile])
        setPreviews([...previews, {
          url: URL.createObjectURL(blob),
          isPdf: false,
          name: newFile.name
        }])
        setShowCamera(false)
      }
    }, 'image/jpeg', 0.8)
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()

    // Validaci칩n: descripcion es obligatorio
    if (files.length === 0 || !monto || !descripcion || descripcion.trim() === '') {
      setError('Por favor completa todos los campos obligatorios: foto, monto y descripci칩n.')
      return
    }

    setLoading(true)
    setError('')
    setSuccess(false)

    try {
      const formData = new FormData()
      files.forEach(file => {
        formData.append('foto', file)
      })
      formData.append('monto', monto)
      formData.append('descripcion', descripcion)
      formData.append('tipo', tipo)

      // Nuevos campos
      formData.append('para', para)
      formData.append('tipo_comprobante', tipoComprobante)
      if (numeroDocumento) formData.append('numero_documento', numeroDocumento)
      if (numeroComprobante) formData.append('numero_comprobante', numeroComprobante)

      formData.append('createTxt', '1')

      await uploadViatico(formData)
      setSuccess(true)
      setTimeout(() => {
        router.push('/mis-viaticos')
      }, 2000)
    } catch (err) {
      setError((err as Error).message || 'Error al subir el vi치tico')
    } finally {
      setLoading(false)
    }
  }

  if (authLoading || !appUser) {
    return (
      <AuthGuard>
        <Layout>
          <div className="max-w-5xl mx-auto space-y-6">
            <Card>
              <CardContent className="p-20">
                <div className="flex flex-col items-center justify-center text-center space-y-4">
                  <Loader2 className="h-12 w-12 animate-spin text-primary" />
                  <p className="text-muted-foreground">Cargando...</p>
                </div>
              </CardContent>
            </Card>
          </div>
        </Layout>
      </AuthGuard>
    );
  }

  if (!appUser.crear_carpeta) {
    return (
      <AuthGuard>
        <Layout>
          <div className="max-w-5xl mx-auto space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Acceso Restringido</CardTitle>
                <CardDescription>No tienes permiso para acceder a esta p치gina</CardDescription>
              </CardHeader>
              <CardContent>
                <Alert variant="destructive">
                  <AlertCircle className="h-4 w-4" />
                  <AlertTitle>Sin Permiso</AlertTitle>
                  <AlertDescription>
                    Tu carpeta de OneDrive no est치 habilitada. No puedes subir vi치ticos en este momento.
                    Por favor, contacta al administrador para habilitar tu cuenta.
                    <br /><br />
                    Ser치s redirigido al dashboard...
                  </AlertDescription>
                </Alert>
              </CardContent>
            </Card>
          </div>
        </Layout>
      </AuthGuard>
    )
  }

  return (
    <AuthGuard>
      <Layout>
        <TooltipProvider>
          <div className="max-w-5xl mx-auto space-y-6">
            <div>
              <h1 className="text-3xl font-bold tracking-tight">Nuevo Vi치tico</h1>
              <p className="text-muted-foreground">
                Registra un nuevo comprobante con su foto o PDF
              </p>
            </div>

            {/* Panel de Fecha Activa y Cierre */}
            <Card className={`border-2 ${isGracePeriod ? 'border-orange-500 bg-orange-50' : 'border-primary/20 bg-primary/5'}`}>
              <CardContent className="pt-6 flex flex-col sm:flex-row items-center justify-between gap-4">
                <div className="flex items-center gap-4">
                  <div className={`p-3 rounded-full ${isGracePeriod ? 'bg-orange-100' : 'bg-primary/10'}`}>
                    <CalendarDays className={`h-8 w-8 ${isGracePeriod ? 'text-orange-600' : 'text-primary'}`} />
                  </div>
                  <div>
                    <p className="text-sm font-medium text-muted-foreground">Fecha de Registro Activa</p>
                    <h2 className={`text-3xl font-bold ${isGracePeriod ? 'text-orange-700' : 'text-primary'}`}>{activeDateDisplay}</h2>
                  </div>
                </div>

                {isGracePeriod && (
                  <div className="flex flex-col items-center sm:items-end gap-2 w-full sm:w-auto">
                    <div className="flex items-center gap-2 text-orange-700 bg-orange-200/50 px-4 py-2 rounded-lg border border-orange-200">
                      <Clock className="h-5 w-5 animate-pulse" />
                      <span className="font-bold text-lg">Cierre en: {timeLeft}</span>
                    </div>
                    <p className="text-xs text-orange-600 font-medium text-center sm:text-right max-w-[250px]">
                      Tienes hasta las 10:00 AM para registrar vi치ticos con fecha de ayer.
                    </p>
                    <Button
                      variant="destructive"
                      size="sm"
                      onClick={handleCloseDay}
                      disabled={closingDay}
                      className="w-full sm:w-auto mt-2 bg-orange-600 hover:bg-orange-700 text-white border-none shadow-md"
                    >
                      {closingDay ? (
                        <Loader2 className="h-4 w-4 animate-spin" />
                      ) : (
                        <Lock className="h-4 w-4 mr-2" />
                      )}
                      Cerrar d칤a ahora
                    </Button>
                  </div>
                )}
              </CardContent>
            </Card>

            <form onSubmit={handleSubmit} className="space-y-6">
              <Card>
                <CardHeader>
                  <CardTitle>Informaci칩n del Comprobante</CardTitle>
                  <CardDescription>Sube tus comprobantes y completa los datos</CardDescription>
                </CardHeader>
                <CardContent className="space-y-6">
                  <div className="space-y-4">
                    <div className="flex items-center justify-between">
                      <Label>Comprobantes</Label>
                      <Tooltip>
                        <TooltipTrigger asChild>
                          <Badge variant="outline" className="text-xs">
                            <Info className="h-3 w-3 mr-1" />
                            Formatos: JPG, PNG, WEBP, PDF
                          </Badge>
                        </TooltipTrigger>
                        <TooltipContent>
                          <p>El tama침o m치ximo es de 10MB por archivo</p>
                        </TooltipContent>
                      </Tooltip>
                    </div>
                    <input
                      ref={fileInputRef}
                      type="file"
                      accept="image/*,application/pdf"
                      onChange={handleFileChange}
                      className="hidden"
                      multiple
                    />
                    {previews.length > 0 ? (
                      <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-4">
                        {previews.map((preview, index) => (
                          <div key={index} className="relative group w-full">
                            <div className="rounded-lg border overflow-hidden aspect-square flex items-center justify-center bg-muted">
                              {preview.isPdf ? (
                                <div className="p-4 flex flex-col items-center justify-center text-center">
                                  <FileText className="w-10 h-10 text-primary" />
                                  <p className="mt-2 text-xs font-medium break-all max-w-xs">{preview.name}</p>
                                  <a href={preview.url} target="_blank" rel="noreferrer" className="mt-2">
                                    <Button variant="outline" size="sm">Ver</Button>
                                  </a>
                                </div>
                              ) : (
                                <img
                                  src={preview.url}
                                  alt={`Vista previa ${index + 1}`}
                                  className="w-full h-full object-cover"
                                />
                              )}
                            </div>
                            <div className="absolute top-2 right-2 opacity-0 group-hover:opacity-100 transition-opacity">
                              <Tooltip>
                                <TooltipTrigger asChild>
                                  <Button type="button" variant="destructive" size="icon" onClick={() => removeFile(index)}>
                                    <X className="h-4 w-4" />
                                  </Button>
                                </TooltipTrigger>
                                <TooltipContent>
                                  <p>Eliminar archivo</p>
                                </TooltipContent>
                              </Tooltip>
                            </div>
                          </div>
                        ))}
                        <div
                          className="border-2 border-dashed rounded-lg flex flex-col items-center justify-center text-center cursor-pointer hover:bg-muted/50 transition-colors aspect-square"
                          onClick={() => fileInputRef.current?.click()}>
                          <Upload className="w-8 h-8 text-primary" />
                          <p className="mt-2 text-sm font-semibold">A침adir m치s</p>
                        </div>
                      </div>
                    ) : (
                      <div
                        className="border-2 border-dashed rounded-lg flex flex-col items-center justify-center p-20 text-center cursor-pointer hover:bg-muted/50 transition-colors"
                        onClick={() => fileInputRef.current?.click()}>
                        <div className="mx-auto w-16 h-16 rounded-lg bg-primary/10 flex items-center justify-center mb-4">
                          <Upload className="w-8 h-8 text-primary" />
                        </div>
                        <p className="mt-4 font-semibold">Seleccionar Archivos</p>
                        <p className="text-sm text-muted-foreground mt-2">o arrastra y suelta aqu칤</p>
                        <div className="mt-6 flex items-center justify-center gap-2 flex-wrap">
                          <Badge variant="outline" className="text-xs">JPG</Badge>
                          <Badge variant="outline" className="text-xs">PNG</Badge>
                          <Badge variant="outline" className="text-xs">WEBP</Badge>
                          <Badge variant="outline" className="text-xs">PDF</Badge>
                        </div>
                        <p className="text-xs text-muted-foreground mt-4">M치ximo 10MB por archivo</p>
                      </div>
                    )}
                    <Button
                      type="button"
                      variant="outline"
                      className="w-full"
                      onClick={startCamera}>
                      <Camera className="h-4 w-4 mr-2" />
                      Tomar y A침adir Foto
                    </Button>
                  </div>

                  <div className="space-y-6 pt-6 border-t">
                    <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                      <div className="space-y-2">
                        <Label htmlFor="para">Para *</Label>
                        <Select value={para} onValueChange={setPara} required>
                          <SelectTrigger id="para">
                            <SelectValue placeholder="Selecciona una opci칩n" />
                          </SelectTrigger>
                          <SelectContent>
                            <SelectItem value="EMPRESA">游끽 Empresa</SelectItem>
                            <SelectItem value="PERSONAL">游녻 Personal</SelectItem>
                          </SelectContent>
                        </Select>
                      </div>

                      <div className="space-y-2">
                        <Label htmlFor="tipo_comprobante">Tipo de Comprobante *</Label>
                        <Select value={tipoComprobante} onValueChange={setTipoComprobante} required>
                          <SelectTrigger id="tipo_comprobante">
                            <SelectValue placeholder="Selecciona un tipo" />
                          </SelectTrigger>
                          <SelectContent>
                            <SelectItem value="FACTURA">游 Factura</SelectItem>
                            <SelectItem value="BOLETA">游 Boleta</SelectItem>
                            <SelectItem value="RECIBO POR HONORARIO">游늯 Recibo por Honorario</SelectItem>
                            <SelectItem value="SIN COMPROBANTE">仇 Sin Comprobante</SelectItem>
                          </SelectContent>
                        </Select>
                      </div>
                    </div>

                    {/* Campo condicional: N칰mero de Documento (RUC/DNI) */}
                    {tipoComprobante && tipoComprobante !== 'SIN COMPROBANTE' && (
                      <div className="space-y-2">
                        <Label htmlFor="numero_documento">
                          {tipoComprobante === 'BOLETA' ? 'N칰mero de DNI' : 'N칰mero de RUC'}
                        </Label>
                        <Input
                          id="numero_documento"
                          type="text"
                          value={numeroDocumento}
                          onChange={(e) => setNumeroDocumento(e.target.value)}
                          placeholder={tipoComprobante === 'BOLETA' ? 'Ej: 12345678' : 'Ej: 20123456789'}
                        />
                      </div>
                    )}

                    <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                      <div className="space-y-2">
                        <Label htmlFor="que_sustenta">Qu칠 Sustenta</Label>
                        <Input
                          id="que_sustenta"
                          type="text"
                          value={queSustenta}
                          disabled
                          className="bg-muted"
                        />
                      </div>

                      <div className="space-y-2">
                        <Label htmlFor="numero_comprobante">N칰mero de Comprobante</Label>
                        <Input
                          id="numero_comprobante"
                          type="text"
                          value={numeroComprobante}
                          onChange={(e) => setNumeroComprobante(e.target.value)}
                          placeholder="Ej: 001-12345"
                        />
                      </div>
                    </div>

                    <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                      <div className="space-y-2">
                        <Label htmlFor="monto">Monto (S/) *</Label>
                        <Input
                          id="monto"
                          type="number"
                          step="0.01"
                          min="0"
                          value={monto}
                          onChange={(e) => setMonto(e.target.value)}
                          required
                          placeholder="0.00"
                        />
                      </div>
                    </div>

                    <div className="space-y-2">
                      <Label htmlFor="descripcion">Descripci칩n *</Label>
                      <Textarea
                        id="descripcion"
                        value={descripcion}
                        onChange={(e) => setDescripcion(e.target.value)}
                        required
                        placeholder="Ej: Almuerzo con cliente, taxi al aeropuerto, etc."
                        rows={4}
                      />
                    </div>


                  </div>

                  {error && (
                    <Alert variant="destructive">
                      <AlertCircle className="h-4 w-4" />
                      <AlertTitle>Error</AlertTitle>
                      <AlertDescription>{error}</AlertDescription>
                    </Alert>
                  )}

                  {success && (
                    <Alert>
                      <CheckCircle2 className="h-4 w-4" />
                      <AlertTitle>칄xito</AlertTitle>
                      <AlertDescription>
                        Vi치tico registrado correctamente. Redirigiendo...
                      </AlertDescription>
                    </Alert>
                  )}
                </CardContent>
                <CardFooter className="flex justify-end">
                  <Button
                    type="submit"
                    size="lg"
                    disabled={loading}
                    className="min-w-[200px]">
                    {loading ? (
                      <>
                        <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                        Registrando...
                      </>
                    ) : (
                      <>
                        <FileCheck className="mr-2 h-4 w-4" />
                        Registrar Vi치tico
                      </>
                    )}
                  </Button>
                </CardFooter>
              </Card>
            </form>

            <Dialog open={showCamera} onOpenChange={setShowCamera}>
              <DialogContent className="max-w-4xl">
                <DialogHeader>
                  <DialogTitle>Tomar Foto</DialogTitle>
                  <DialogDescription>
                    Captura una foto de tu comprobante usando la c치mara
                  </DialogDescription>
                </DialogHeader>
                <div className="space-y-4">
                  <div className="relative rounded-lg overflow-hidden bg-black">
                    <video
                      ref={videoRef}
                      id="video"
                      autoPlay
                      playsInline
                      className="w-full rounded-lg"
                    />
                    <Button
                      variant="secondary"
                      size="icon"
                      className="absolute top-4 right-4 rounded-full bg-black/50 hover:bg-black/70 text-white border-none"
                      onClick={switchCamera}
                    >
                      <RefreshCcw className="h-5 w-5" />
                    </Button>
                  </div>
                  <canvas id="canvas" style={{ display: 'none' }} />
                  <div className="flex gap-3 justify-end">
                    <Button variant="outline" onClick={() => setShowCamera(false)}>
                      Cancelar
                    </Button>
                    <Button onClick={capturePhoto}>
                      <Camera className="h-4 w-4 mr-2" />
                      Capturar Foto
                    </Button>
                  </div>
                </div>
              </DialogContent>
            </Dialog>
          </div>
        </TooltipProvider >
      </Layout >
    </AuthGuard >
  )
}
